@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   margin:0;
}

body {
  font-family: 'Lato', 'Helvetica Neue', sans-serif;
  background-color: #F4F4F7;
}

h1 {
  font-family: 'Anton', sans-serif;
  margin:0;
}

a.link-unstyled {
  color: inherit;
}

a.link-unstyled:hover {
  color: inherit;
  text-decoration: none;
}

.btn {
  padding: 0.375rem 2rem;
}

.btn.btn-primary {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #7DCD40;
  border-color: #7DCD40;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(125, 205, 64, 0.5);
}

.btn.btn-danger {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
  color: #fff;
  background-color: #CD4040;
  border-color: #CD4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
  box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}
.header-container{
	display: flex;
	flex-direction: row;
	align-items: center;
	justify-content:space-between;
	padding:30px;
	background-color: #2f3045;
	color: white;
	position: -webkit-sticky;
	position: sticky;
  top: 0;
  z-index: 999;
	height: 10vh;
}

.header-start,
.header-wallet{
	display:flex;
	align-items:center;
}
.header-avatar{
	width: 70px;
	height: 70px;
	border-radius: 50%;
	margin-right: 5px;
}

.header-wallet{
	flex-direction: column;
	flex-wrap: nowrap;
	border-radius: 5px;
	justify-content:space-between;
	min-width: 170px;
	grid-gap: 10px;
	gap: 10px;
	padding: 5px 10px;
	cursor: pointer;
	height: 30px;
}

.wallet-address{
	border-radius: 5px;
	position: absolute;
	top: 68%;
	right: 30px;
	z-index: 1000;
	border: 1px solid #6a668a;
	background-color: #2f3045;
	height: auto;
	width: 310px;
	display:flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	cursor: auto;
}
.wallet-tittle{
	font-size:1.5em;
	text-decoration: none;
	color: #fff;
}
.wallet-tittle:hover{
	-webkit-text-decoration-line:underline;
	        text-decoration-line:underline;
}
.wallet-tittle:active{
	color: rgba(255, 255, 255, 0.438);
}
.wallet-tittle::after{
	display: inline-block;
    margin-left: .255em;
    vertical-align: .100em;
    content: "";
    border-top: .3em solid;
    border-right: .3em solid transparent;
    border-bottom: 0;
    border-left: .3em solid transparent;
}
.link{
	font-size: 1em;
	margin: 0;
	text-decoration: none;
	color: #fff;
	cursor: pointer;
}

.address-link{
	margin: 0 0 15px 0;
	text-decoration: none;
	color: #fff;
	font-size: .65em;
	cursor: pointer;
}
.address-link:hover,.link:hover{
	-webkit-text-decoration-line:underline;
	        text-decoration-line:underline;
	color: #03e1e4;
}

.image-coin{
	margin-right: 5px;
	display: flex;
	flex-direction: row;
	grid-gap: 5px;
	gap: 5px;
	width:30px;
	height:30px;
}
.image-coin>img {width:100%;height:100%;}

.address-tittle{
	display:flex;
	flex-direction: row;
	align-items: center;
	justify-content: center;
	width: auto;
	grid-gap: 5px;
	gap: 5px;
	margin: 10px 0;
}
.change-coin{
	position:absolute;
	top: 150px;
	right:0;
	cursor: pointer;
	height: 35px;
	width: 25px;
	font-size: 1.3rem;
	text-align: center;
	border-radius: 5px;
	box-shadow: 0 6px #514e72, 0 10px #1f1f2c;
	background-color: #8984b1;
	border: 1px solid #c3b0d0;
	color: #2f3045;
	transition: box-shadow .1s ease-in-out;
}
.change-coin:active{
	top: 155px;
	box-shadow: none;
}

.qr-wallet{
	max-width: 256px;
	max-height:256px;
}
.copied{
	margin: 5px 0 0 0;
	color: #d735bc;
	font-weight: bold;
}
.footer-container{
	position: -webkit-sticky;
	position: sticky;
	bottom: 0;
	display: grid;
	justify-content: space-between;
	height: 15vh;
	padding:10px;
	align-items: center;
	grid-gap: 10px;
	gap: 10px;
	background-color: #2f3045;
	color: white;
  z-index: 999;
	margin: 0;
	grid-template-areas:"logo promo"
											"social social";
}

@media (min-width: 988px) {
  .footer-container {
		padding:0 30px;
		grid-template-areas:"logo social promo";
		grid-gap: 20px;
		gap: 20px;
		height: 80px;
  }
}

.social-container{
	grid-area: social;
	display: flex;
	flex-direction: row;
	grid-gap: 25px;
	gap: 25px;
	list-style: none;
	justify-content: center;
}	
.icons-container{
	cursor: pointer;
}

.icons{
	color: white;
	font-size: 1.5em;
	-webkit-text-decoration-line:none;
	        text-decoration-line:none;
}
.icons-container:hover{
	-webkit-text-decoration-line:underline;
	        text-decoration-line:underline;
}
.icons-container:active{
	color: rgba(255, 255, 255, 0.438);
}

.promo-container{
	grid-area: promo;
	display: inline-block;
	min-width: 100px;
}

.promo-container>img{
	width: 100%;
	height: 100%;
}

.footer-tittle{
	grid-area: logo;
	display: flex;
	flex-direction: row;
	align-items: center;
}
.logo-container{
	display: inline-block;
	width: 200px;
	height: 60px;
}
.logo-nick{
	width: 100%;
	height: 100%;
}
.container {
	height: 100%;
	display: flex;
	justify-content: space-evenly;
	align-content: center;
	align-items: center;
	flex-wrap: wrap;
	grid-gap: 10px;
	gap: 10px;
	margin: 0 auto;
	padding:0.7em 0;
	background:#181928;
}
.info-container{
	font-size: 12px;
	border-radius: 5px;
	border: 1px solid #fff;
	width: 130px;
	display: flex;
	flex-direction: column;
	justify-content:center;
	align-items: center;
	grid-gap:5px;
	gap:5px;
	padding:.4em;
	color: #fff;
	background-color: #2f3045;
}
.tittle-coin{
	display: flex;
	align-items: center;
}
.image-coin{
	margin-right: 5px;
	display: flex;
	justify-content: center;
	align-items: center;
	width:30px;
	height:30px;
}
.image-coin>img {
	width:100%;
	height:100%;
}

.green{
	color:rgb(8, 252, 8);
}
.red{
	color: rgb(252 56 56);
}
.table-container{
	font-size: 24px;
}
.col-container{
	width: 100%;
}
.col{
	width: 100%;
	justify-content:space-between;
	color: #03e1e4;
}
.table-tittles{
	font-size: 1.7rem;
}

.info{
	width:100%;
	text-align: center;
	color: #03e0e48a;
}
.row>td>h4{
	color: #d735bcd3;
	font-size: 1.3rem;
}
input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
  -webkit-appearance: none; 
  margin: 0; 
}

input[type=number] { -moz-appearance:textfield; }
.form{
	min-width: 412px;
	height: auto;
	display: flex;
	flex-direction: column;
	background-image: linear-gradient(0deg,#514e72,#2f3045);
	border-radius: 10px;
	padding: 10px;
	margin:0.5em;
}
.form-subTitle{
	display:flex;
	justify-self: center;
	color: #03e1e4;
}

.label{
	font-size: 1.3rem;
	font-weight: 500;
}
.right{
	margin:0 15px 0 13px;
}
.form-title{
	text-align:center;
	margin-bottom: 20px;
}
.selector{
	font-size: 1rem;
	font-weight: 400;
	line-height: 1.3;
	margin: 0 0.3rem;
	padding: 0.3rem 0.5rem 0.3rem .3rem;
	background:none;
	color: #fff;
	border-radius: 5px;
}
option{
	color: black;
	background:none;
}
.form-group{
	display: flex;
	width: 100%;
	align-items: center;
	justify-content: space-between;
	margin: 0.2rem 0;
}
.form-control{
	font-size: 1rem;
	text-align: end;
	width: 100px;
	border: 3px solid #03e0e48a;
	color: #fff;
	border-radius: 5px;
	background-color: transparent;
}
.reward{
	margin:0 10px;
	max-width: 70px;
	font-size: 24px;
}
.form-control:focus-visible{
	border: 3px solid #03e1e4;
	outline: none;
}
.btn{
	margin: .3rem 0;
	font-size: 1.3rem;
	align-self: center;
	width: auto;
	height: auto;
	background-color:#03e1e4;
	border-radius: 5px;
	box-shadow: 0 6px #186c6d, 0 10px #1f1f2c;
	border: 1px solid #9ff2f3;
	color: #2f3045;
	padding: 8px 20px;
	transition: box-shadow .1s ease-in-out;
}
.btn:active{
	position: relative;
	top: 5px;
	box-shadow: none;
}
.global-container{
	background:#181928;
	margin: 0;
	color: #ffffff;
	height: 90vh;
	display: grid;
	justify-content: center;
  align-items: center;
	grid-template-rows: 1fr 1fr;
	padding: 10px 50px;
}
.mid-table{
	display: flex;
	flex-direction: column;
	align-items: center;
	grid-gap: 20px;
	gap: 20px;
	justify-content: center;
	margin:1em;
}
h1{
	letter-spacing: 0.2em;
	font-weight: 200;
}
.mid-widget{
	height: auto;
}

@media (min-width: 988px) {
  .global-container {
    grid-template-columns: 1fr 1fr;
		grid-template-rows: 1fr;
		height: 45vh;
  }
}
